.main-container {
  min-height: 100vh;
  background-color: var(--bg-primary);
  transition: all 0.3s ease;
}

.store-main-content {
  margin-left: 0;
  transition: margin-left 0.3s ease;
  
  &.left-panel-open {
    margin-left: 250px;
  }
}

.search-results {
  max-width: 1400px;
  margin: 0 auto;
  padding: 2rem;
}

// 검색 결과 헤더
.search-results__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding-bottom: 1.5rem;
  border-bottom: 2px solid var(--border-color);
  flex-wrap: wrap;
  gap: 1rem;
}

.search-results__info {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.search-results__title {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1.75rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0;

  .search-icon {
    font-size: 2rem;
    width: 2rem;
    height: 2rem;
    color: var(--accent-primary);
  }

  span {
    color: var(--accent-primary);
  }
}

.search-results__count {
  font-size: 1rem;
  color: var(--text-secondary);
  margin: 0;
}

// 정렬 버튼
.search-results__sort {
  display: flex;
  gap: 0.5rem;
}

.sort-btn {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  padding: 0.75rem 1.25rem;
  border: 1px solid var(--border-color);
  background-color: var(--bg-secondary);
  color: var(--text-secondary);
  border-radius: 8px;
  font-size: 0.95rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;

  mat-icon {
    font-size: 1.25rem;
    width: 1.25rem;
    height: 1.25rem;
  }

  &:hover:not(:disabled) {
    background-color: var(--bg-primary);
    border-color: var(--accent-primary);
    color: var(--text-primary);
  }

  &--active {
    background-color: var(--accent-primary);
    border-color: var(--accent-primary);
    color: white;

    &:hover {
      background-color: var(--accent-primary);
      opacity: 0.9;
    }
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

// 로딩 상태
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 0;
  gap: 1rem;
}

.loading-text {
  color: var(--text-secondary);
  font-size: 1rem;
}

// 빈 상태
.empty-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 0;
  gap: 1rem;
}

.empty-icon {
  font-size: 5rem;
  width: 5rem;
  height: 5rem;
  color: var(--text-secondary);
  opacity: 0.5;
}

.empty-message {
  color: var(--text-primary);
  font-size: 1.25rem;
  font-weight: 600;
  margin: 0;
}

.empty-hint {
  color: var(--text-secondary);
  font-size: 1rem;
  margin: 0;
}

// 상품 그리드
.search-results__grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

// 상품 카드
.product-card {
  background-color: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: var(--shadow);

  &:hover {
    transform: translateY(-4px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
    border-color: var(--accent-primary);
  }
}

.product-card__image-wrapper {
  position: relative;
  width: 100%;
  padding-top: 100%; // 1:1 비율
  overflow: hidden;
  background-color: var(--bg-primary);
}

.product-card__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;

  .product-card:hover & {
    transform: scale(1.05);
  }
}

.product-card__badge {
  position: absolute;
  top: 0.75rem;
  right: 0.75rem;
  padding: 0.375rem 0.75rem;
  border-radius: 6px;
  font-size: 0.75rem;
  font-weight: 700;
  color: white;
  z-index: 1;

  &--new {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  }

  &--discount {
    background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
    top: 3rem;
  }
}

.product-card__content {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.product-card__store {
  display: flex;
  align-items: center;
  font-size: 0.85rem;
  color: var(--text-secondary);

  span {
    display: flex;
    align-items: center;
    gap: 0.25rem;
    cursor: pointer;
    transition: color 0.2s ease;

    &:hover {
      color: var(--accent-primary);
    }
  }

  mat-icon {
    font-size: 1rem;
    width: 1rem;
    height: 1rem;
  }
}

.product-card__name {
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-height: 1.4;
  min-height: 2.8em;
}

.product-card__price-wrapper {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.product-card__price-main {
  display: flex;
  align-items: center;
}

.product-card__price {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--text-primary);
}

.product-card__price-original {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.85rem;

  .original-price {
    color: var(--text-secondary);
    text-decoration: line-through;
  }

  .discount-rate {
    color: var(--error-color);
    font-weight: 600;
  }
}

.product-card__shipping {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  font-size: 0.85rem;
  color: var(--text-secondary);
  padding-top: 0.5rem;
  border-top: 1px solid var(--border-color);

  mat-icon {
    font-size: 1rem;
    width: 1rem;
    height: 1rem;
  }

  span {
    font-weight: 500;
  }
}

// 무한 스크롤 감지 요소
.observer-target {
  height: 1px;
  margin-top: 2rem;
}

// 추가 로딩
.loading-more {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  padding: 2rem 0;

  p {
    color: var(--text-secondary);
    font-size: 0.9rem;
    margin: 0;
  }
}

// 더 이상 상품 없음
.no-more {
  text-align: center;
  padding: 2rem 0;
  color: var(--text-secondary);
  font-size: 0.95rem;

  p {
    margin: 0;
  }
}

// 반응형 디자인
@media (max-width: 1200px) {
  .search-results__grid {
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  }
}

@media (max-width: 768px) {
  .search-results {
    padding: 12px 0; // 좌우 패딩 제거하여 100% 너비 활용
  }

  .search-results__header {
    flex-direction: column;
    align-items: flex-start;
    padding: 0 12px 1rem; // 좌우 최소 패딩만
    margin-bottom: 1rem;
  }

  .search-results__title {
    font-size: 1.5rem;
  }

  .search-results__sort {
    width: 100%;
    justify-content: space-between;
  }

  .sort-btn {
    flex: 1;
    justify-content: center;
    padding: 0.625rem 0.75rem;
    font-size: 0.85rem;

    mat-icon {
      font-size: 1.1rem;
      width: 1.1rem;
      height: 1.1rem;
    }
  }

  .search-results__grid {
    grid-template-columns: 1fr; // 모바일에서 1열로 변경
    gap: 12px; // 간격 조정
    padding: 0 12px; // 최소 패딩
  }

  .store-main-content.left-panel-open {
    margin-left: 0;
  }
}

@media (max-width: 480px) {
  .search-results__grid {
    grid-template-columns: 1fr;
    gap: 12px;
  }
}
